                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
          </div>
          </div>
          <div class="calc">
            <div class="img-left"><img class="spxx-img" src="/static/images/category/makesureorder/spxx.png" /></div>
            <div class="shop-right">
              <div class="name">新鲜紫茄子 绿色食品</div>
              <div class="standard">规格: 5kg</div>
              <div class="price-amount">
                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
            </div>
          </div>
          <div class="calc">
            <div class="img-left"><img class="spxx-img" src="/static/images/category/makesureorder/spxx.png" /></div>
            <div class="shop-right">
              <div class="name">新鲜紫茄子 绿色食品</div>
              <div class="standard">规格: 5kg</div>
              <div class="price-amount">
                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
            </div>
          </div>
          <div class="calc">
            <div class="img-left"><img class="spxx-img" src="/static/images/category/makesureorder/spxx.png" /></div>
            <div class="shop-right">
              <div class="name">新鲜紫茄子 绿色食品</div>
              <div class="standard">规格: 5kg</div>
              <div class="price-amount">
                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
            </div>
          </div>
          <div class="calc">
            <div class="img-left"><img class="spxx-img" src="/static/images/category/makesureorder/spxx.png" /></div>
            <div class="shop-right">
              <div class="name">新鲜紫茄子 绿色食品</div>
              <div class="standard">规格: 5kg</div>
              <div class="price-amount">
                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
            </div>
          </div>
          <div class="calc">
            <div class="img-left"><img class="spxx-img" src="/static/images/category/makesureorder/spxx.png" /></div>
            <div class="shop-right">
              <div class="name">新鲜紫茄子 绿色食品</div>
              <div class="standard">规格: 5kg</div>
              <div class="price-amount">
                <div class="price">￥ 27.80</div>
                <div class="amount">x2</div>
              </div>
            </div>
          </div>
        </div>
        <div class="tips">
            <van-field placeholder="请输入备注" label="备注留言"/>
        </div>
      </div>
      <div class="payinfo">
        <TitleSecond title="付款信息" :hideRight="true" class="title-second1" :size="smallsize"></TitleSecond>
        <div class="coin">
          <div class="coin-pay">
            <img class="yn-img" src="/static/images/category/makesureorder/yn.png" />
            <div class="pay-type">钱包支付 (余额：2000)</div>
          </div>
          <div class="wxz-img"><img class="checked-img" @click="changepic" :src="check"/></div>
        </div>
        <div class="wx">
          <div class="wx-img">
            <img class="checked-img"  src="/static/images/category/makesureorder/wx.png" />
            <div class="pay-type">微信钱包支付</div>
          </div>
          <div class="wxz-img"><img class="checked-img" @click="changepic1" :src="check1"/></div>
        </div>
      </div>
      <div class="pay-bottom">
        <div class="auto-express">
          <div class="checked-img" @click="express=!express"><img :src="expresscheck" /><span>自动配送</span></div>
          <div class="pay-order">
            <div class="count-cost">
              <div class="total"><span>共计:</span> 27.80元</div>
              <div class="express">配送费: 免配送费</div>
