<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
export default {
  name: 'makesureorder',
  components: {
    PageContent,
    TitleSecond
  },
  data () {
    return {
      // 切换图片变量
      toggle: false,
      toggle1: false,
      express: false,
      smallsize: 'smallsize',
      ishaveaddress: false
    }
  },
  methods: {
    // 金币支付和微信支付切换变量控制方法
    changepic () {
      // 如果金币支付已经点击的话 微信支付不能同时选择
      if (this.toggle1 === true) {
        this.toggle1 = false
        this.toggle = true
      } else {
        this.toggle = !this.toggle
      }
    },
    changepic1 () {
      // 同上
      if (this.toggle === true) {
        this.toggle = false
        this.toggle1 = true
      } else {
        this.toggle1 = !this.toggle1
      }
    },
    tomyaddress () {
      this.$router.push({path: '/myadress'})
    }
  },
  computed: {
    // 勾选金币支付切换图片方法
    check: function () {
      if (this.toggle === true) {
        return '/static/images/commonicon/xz.png'
      } else if (this.toggle === false) {
        return '/static/images/commonicon/wxz.png'
      } else {
        return '/static/images/commonicon/wxz.png'
      }
    },
    // 勾选微信钱包支付切换图片方法
    check1: function () {
      if (this.toggle1 === true) {
        return '/static/images/commonicon/xz.png'
      } else if (this.toggle1 === false) {
        return '/static/images/commonicon/wxz.png'
      } else {
        return '/static/images/commonicon/wxz.png'
      }
    },
    // 勾选自动配送切换图片方法
    expresscheck: function () {
      if (this.express === false) {
        return '/static/images/commonicon/wxz.png'
      } else if (this.express === true) {
        return '/static/images/commonicon/xz.png'
      } else {
        return '/static/images/commonicon/wxz.png'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "@/styles/variables.scss";
.app-page{}
.page-content{
  display: flex;
  flex-direction: column;
  .receiveinfo{
    font-family: $font-family-default;
    font-size: 28px;
    height: 220px;
    display: flex;
    background-image: url("/static/images/category/makesureorder/bj.png");
    background-size: cover;
    background-repeat: no-repeat;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
    width: 100%;
    .receive {
      background-color: white;
      display: flex;
      width: 90%;
      height: 80%;
      align-items: center;
      border-radius: 10px;
      .icon-left {
        width: 10%;
        text-align: center;
        img {
          width: 34px;
          height: 42px;
        }
      }
      .arrow {
        width: 8%;
        text-align: center;
        img{
          height: 30px;
        }
      }
      .receive-center {
        flex: 1;
        display: flex;
        flex-direction: column;
        height: 60%;
        justify-content: space-around;
        .receiver{
          font-size: 28px;
          color: $black333333;
          font-weight: normal;
        }
        .address{
          font-size: 28px;
          color: $black333333;
          font-weight: normal;
        }
      }
      .empty-address{
        height: 50%;
        display: flex;
        align-items: center;
        margin-left: 30px;
        img{
          width: 36px;
          height: 36px;
        }
        span{
          margin-left: 15px;
          color: $green40672b;
          font-size: 40px;
          font-family: $font-family-Regular;
        }
      }
    }
    }
  .shopinfo{
    width: 100%;
    font-size: 30px;
    font-family: $font-family-default;
    display: flex;
    flex-direction: column;
    height: calc(100% - 600px);
    overflow: auto;
    border-bottom: 20px solid $whitef5f5f5;
    .title-second1{
      font-weight: normal;
      padding-left: 30px;
      padding-top: 20px;
      margin-bottom: 20px;
      font-size: 24px;
    }
    .shopdetail {
      background-color: #fafafa;
      height: calc(100% - 20px);
      overflow: auto;
      .calc {
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        padding: 0 30px 0 30px;
        .img-left {
          width: 20%;
          img {
            height: 100%;
            width: 100%;
          }
        }
        .shop-right {
          padding: 0 30px 0 30px;
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content:space-evenly;
          height: 100%;
          .name {
            font-size: 30px;
            color: $black333333;
            font-weight: bold;
          }
          .standard {
            font-size: 20px;
            color: $black999999;
          }
          .price-amount {
            display: flex;
            justify-content: space-between;
            .price {
              font-size: 30px;
              color: $redc8251d;
            }
            .amount {
              font-size: 30px;
              color: $black666666;
            }
          }
        }
      }
    }
    .tips{
      .van-cell{
        font-size: 28px;
        color: $black333333;
        align-items: center;
        font-family: $font-family-default;
        background-color: #fafafa;
      }
      /deep/ .van-field .van-cell__title{
        max-width: 20%;
        white-space: nowrap;
      }
      /deep/ .van-field__control {
        font-size: 26px;
        color: #333333;
        font-family: $font-family-default;
      }
    }
  }
  .payinfo{
    font-family: $font-family-default;
    font-size: 28px;
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    .title-second1{
      width: 100%;
      font-weight: normal;
      padding-left: 30px;
      padding-top: 20px;
      margin-bottom: 20px;
      font-size: 24px;
    }
    .coin{
      padding: 0 30px 0 30px;
      display: flex;
      justify-content: space-between;
      height: 20%;
      align-items: center;
      .coin-pay{
        display: flex;
        align-items: center;
        img{
          height: 48px;
          width: 48px;
        }
      }
      .pay-type{
        font-size: 28px;
        color: $black333333;
        margin-left: 15px;
      }
      .wxz-img{
        .checked-img{
          height: 48px;
          width: 48px;
        }
      }
    }
    .wx{
      padding: 0 30px 0 30px;
      display: flex;
      justify-content: space-between;
      height: 20%;
      align-items: center;
      .wx-img{
        display: flex;
        align-items: center;
        img{
          height: 48px;
          width: 48px;
        }
      }
      .pay-type{
        font-size: 28px;
        color: $black333333;
        margin-left: 15px;
      }
      .wxz-img{
        img{
          height: 48px;
          width: 48px;
        }
      }
    }
  }
  .pay-bottom{
    font-size: 28px;
    height: 90px;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    .auto-express{
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: #f7f7f7;
      .checked-img{
        margin-left: 20px;
        width: 35%;
        height: 100%;
        display: flex;
        align-items: center;
        img{
          height: 48px;
          width: 48px;
        }
        span{
          margin-left:10px;
          font-size: 28px;
          color: $black999999;
        }
      }
      .pay-order{
        flex: 1;
        display: flex;
        align-items: center;
        height: 100%;
        justify-content: flex-end;
        .count-cost{
          width: 50%;
          .total{
            font-size: 30px;
            color: $redc8251d;
            span{
              font-size: 28px;
              color: $black666666;
            }
          }
          .express{
            font-size: 20px;
            color: $black999999;
            margin-top: 10px;
          }
        }
        .buy{
          width: 60%;
          height: 100%;
          .van-button{
            height: 100%;
            width: 100%;
            font-size: 30px;
            font-family: $font-family-default;
          }
          .van-button--primary{
            background-color: $green22ac38;
            border: 1px solid $green22ac38;
          }
        }
      }
    }
  }
}

</style>



